.info_card {
    border: 2px solid black;
    font-family: 'Times New Roman', Times, serif;
    font-style: italic;
}

.product-card {
    border: double black;
    border-radius: 5%;
    padding: 2%;
    width: 50%;
    text-align: center;
}

.submit {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    font-size: large;
    font-weight: bold;
    font-style: italic;
    background-color: whitesmoke;
    border: double black;
    border-radius: 15%;
}

.product_list {
    text-align: left;
    margin-left: auto;
    margin-right: auto;
}

.products {
    align-items: grid;
    margin-bottom: 2%;
    display: flex;
    flex-direction: row;
    gap: 5px;
    justify-content: center;
    align-items: baseline;
    flex-wrap: wrap;
}

.logButton {
    text-align: right;
    align-items: grid;
    margin-right: 2%;
}

.cart_items {
    border: double black;
    margin-top: 2%;
    margin-bottom: 2%;
    margin-right: 20%;
    margin-left: 20%;
    border-radius: 10%;
}

.checkout_box {
    align-items: right;
    border: 2px solid black;
    padding: 2%;
}